.popup {
  position: fixed;
  top: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  visibility: collapse;
  opacity: 0;
  transition: all 0.5s ease;
  display: flex;
}
